<template>
  <div>
    <div>
      <el-row :gutter="10">
        <el-col :span="4" :sm="5"
          ><el-input
            size="small"
            v-model="userInput"
            placeholder="名称"
            @keyup.enter.native="chaXun"
          ></el-input>
        </el-col>
        <el-col :span="4" :sm="5">
          <el-select v-model="selectValue" placeholder="请选择" size="small">
            <el-option
              v-for="item in selectOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="4" :sm="6">
          <el-button-group>
            <el-button
              @click="shuaXin"
              size="small"
              type="primary"
              icon="el-icon-refresh"
            ></el-button>
            <el-button @click="chaXun" size="small" type="primary">查询</el-button>
            <el-button @click="addType" size="small" type="primary">添加</el-button>
          </el-button-group>
        </el-col>
        <el-col :span="10"></el-col>
      </el-row>
      <div id="Table">
        <el-table :data="tableData" style="width: 100%" :key="Math.random()">
          <!-- :key="Math.random()" 可以实现表格自动刷新 -->
          <el-table-column label="id" width="100">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.type_id }}</span>
            </template>
          </el-table-column>
          <el-table-column label="名称" width="250" >
            <template slot-scope="scope">
              <span>{{ scope.row.type_pid }}-{{ scope.row.type_name }}</span>
            </template>
          </el-table-column>
          <el-table-column label="状态" width="150" >
            <template slot-scope="scope">
              <el-tag v-show="scope.row.type_status == 1" type="success"
                >启用</el-tag
              >
              <el-tag v-show="scope.row.type_status == 0" type="danger"
                >关闭</el-tag
              >
            </template>
          </el-table-column>
          <el-table-column label="上级" width="190" >
            <template slot-scope="scope">
              <span>{{ scope.row.type_pid }}</span>
            </template>
          </el-table-column>
          <el-table-column label="排序" width="180" >
            <template slot-scope="scope">
              <span>{{ scope.row.type_sort }}</span>
            </template>
          </el-table-column>
          <el-table-column label="关键字" width="220">
            <template slot-scope="scope">
              <span>{{ scope.row.type_en }}</span>
            </template>
          </el-table-column>

        </el-table>
      </div>
      <div>
        <el-dialog title="编辑分类" :visible.sync="dialogOpen">
          <div>
            <el-form :model="editTable" label-width="80px" ref="editUser">
              <el-form-item label="id" >
                <span class="editId">{{editTable.type_id}}</span>
              </el-form-item>
              <el-form-item label="名称">
                <el-input v-model="editTable.type_name"></el-input>
              </el-form-item>
              <el-form-item label="状态">
                <el-input v-model="editTable.type_status"></el-input>
              </el-form-item>
              <el-form-item label="上级">
                <el-input v-model="editTable.type_pid"></el-input>
              </el-form-item>
              <el-form-item label="排序">
                <el-input v-model="editTable.type_sort"></el-input>
              </el-form-item>
              <el-form-item label="关键字">
                <el-input v-model="editTable.type_en"></el-input>
              </el-form-item>
            </el-form>
          </div>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogOpen = false">取 消</el-button>
            <el-button type="primary" @click="editTrue">确 定</el-button>
          </div>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
import service from "@/utils/request";
export default {
  name: "Category",
  data() {
    return {
      dialogOpen: false,
      editTable: { 
        type_id:'',
        type_name:'',
        type_status:'',
        type_pid:'',
        type_sort:'',
        type_en:''
      },
      tableData: [],
      DataOld: [],
      search: "",
      selectOptions: [
        {
          value: 1,
          label: "启用",
        },
        {
          value: 0,
          label: "关闭",
        },
        {
          value: "all",
          label: "全部",
        },
      ],
      selectValue: "all",
      userInput: "",
      AddorUpt:'',
    };
  },
  methods: {
    handleEdit(index, row) {
      // 编辑
      // this.editUser=this.tableData[index]  //有错
      this.dialogOpen = true;
      this.AddorUpt='upt';  //更新
      this.editTable = Object.assign({}, row); //引用值的深拷贝问题,可以用Object.prototype.assign
      this.editTable.EditIndex = index;
    },
    editTrue() {
      // 修改结束
      service
      .post("/admin/category", { method: this.AddorUpt , edit : this.editTable })
      .then((result) => {
        // console.log("result",result.data[0].changedRows);
        if(result.data[0].changedRows==1){  //影响行数
        this.tableData[this.editTable.EditIndex] = this.editTable;
        this.dialogOpen = false;
        this.$message({message: "修改成功！",type: "success", });
        }else{this.$message.error('修改失败');}
      })
      .catch((err) => {
        console.log(err);
        this.$message.error('修改失败');
      });
  
    },
    handleDelete(index, row) {
      // 删除元素
      console.log(index, row);
      service.post("/admin/category", { method: "del",edit:row })
      .then((result) => {
        console.log("result",result.data[0]);
       if(result.data[0].affectedRows==1){  //影响行数
        this.tableData.splice(index, 1);
        this.$message({message: "删除成功！",type: "success",});
        }else{
          this.$message.error('删除失败');
        }
      })
      .catch((err) => {
        console.log(err);
        this.$message.error('删除失败');
      });

    },
    shuaXin() {
      location.reload();
    },
    chaXun() {
      // 查找名称  并显示到表格
      console.log(this.userInput);
      this.tableData=this.DataOld;
      this.tableData = this.tableData.filter((data) => {
        return (
          data.type_name == this.userInput || this.userInput == ""
        );
      });
      
    },
    addType(){
      // 添加分类
      this.AddorUpt='add'; //设置请求类别
      this.editTable={
        type_id:'',
        type_name:'',
        type_status:'',
        type_pid:'',
        type_sort:'',
        type_en:''
      };//设置空   否则双向绑定出错
      this.dialogOpen = true;
      
    }
  },
  watch: {
    selectValue() {
      this.tableData = this.DataOld;
      console.log(this.selectValue);
      let b = this.tableData.filter((data) => {
        // console.log("data:",data);
        return (
          data.type_status === this.selectValue || this.selectValue === "all"
        );
      });
      //filter() 方法创建一个新的数组，新数组中的元素是通过检查指定数组中符合条件的所有元素。
      this.tableData = b;
    },
  },
  mounted() {
    service
      .post("/admin/category", { method: "get" })
      .then((result) => {
        // console.log("about", result.data);
        // console.log("result",result.data[0]);
        this.DataOld = result.data[0];
        this.tableData = this.DataOld;
      })
      .catch((err) => {
        console.log(err);
        this.$message.error('查询失败');
      });
  },
};
</script>

<style scoped>
#Table {
  margin-top: 15px;
}
.editId{
  float: left;
}
</style>